/* Sliding pages */

aside {
position: fixed;
top:0;
left:-100%;
height: 100%;
width:100%;
background-color: #000;
transition: .8s cubic-bezier(.97,.19,.03,.94);
z-index: 1;
}

#one.on {
left: 0;
background-color: red;
}

#two.on{
left: 0;
background-color: orange;
transition-delay: .3s;
} 

#tree.on {
left:0;
