.contact{
  position: absolute;
  top:5%;
  left:50%;
  transform: translateX(-50%);
}

.contact h1{
  text-align: center;
  margin-bottom: 20px;
}

.contact textarea{
    display: block;
    margin: 0 auto 0px;
    font-size: 16px;
    width:70vw;
    min-height: 300px;
    line-height: 1.5;
box-shadow: 0 0 15px black;
}


.contact button{
    display: block;
    margin: 20px auto;
    border: 2px solid black;
    padding: 10px 30px;
    background-color: #fff;
    cursor: pointer;
    transition: .3s;
}

.contact button:hover{
background-color: black;
color: white;
}